'use client';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { LocaleContext } from '~src/context/Context/LocaleContext';
const useIntlHelper = ()=>{
    const { activeLocale, locales } = React.useContext(LocaleContext);
    const intl = useIntl();
    const formatMessage = (id)=>intl.formatMessage({
            id: id
        });
    const formatDate = (date, options)=>options ? intl.formatDate(date, options) : intl.formatDate(date, locales[activeLocale].formatOptions);
    return {
        intl,
        activeLocale,
        locales,
        formatMessage,
        formatDate
    };
};
export default useIntlHelper;
