export function getMetadata(data, template, site, formatMessage) {
    if (template === 'home') {
        const seo = {
            title: site.title,
            desc: site.desc,
            image: site.image
        };
        return seo;
    }
    if (template === 'notfound') {
        const seo = {
            title: site.notFound,
            desc: site.desc,
            image: site.image
        };
        return seo;
    }
    if (template === 'page') {
        const seo = {
            title: data.page?.frontmatter?.title,
            desc: data.page?.excerpt,
            image: site.image
        };
        return seo;
    }
    if (template === 'post') {
        const seo = {
            title: data.post?.frontmatter?.title,
            desc: data.post?.excerpt,
            image: `${site.url}${data.post?.frontmatter?.image?.childImageSharp?.gatsbyImageData?.images?.fallback?.src}`,
            date: data.post?.frontmatter?.date
        };
        return seo;
    }
    if (template === 'tag') {
        const seo = {
            title: formatMessage(`tags.${data.tag.yamlId}.title`),
            desc: formatMessage(`tags.${data.tag.yamlId}.desc`),
            image: data.tag.image ? `${site.url}${data.tag.image.childImageSharp.gatsbyImageData.images.fallback.src}` : site.image
        };
        return seo;
    }
    if (template === 'film') {
        const seo = {
            title: 'Filmography',
            desc: site.title,
            image: site.image
        };
        return seo;
    } else {
        const seo = {
            title: site.title,
            desc: site.desc,
            image: site.image
        };
        return seo;
    }
}
